body {
  font-family: sans-serif;
  margin: 0;
  padding: 0;
  background: #f4f4f4;
}

.container {
  padding: 20px;
  text-align: center;
}

.container button {
  display: block;
  margin: 10px auto;
  padding: 12px 20px;
  font-size: 16px;
  border: none;
  background: #0088cc;
  color: white;
  border-radius: 8px;
  cursor: pointer;
  transition: background 0.3s;
}

.container button:hover {
  background: #006ea8;
}

.outside {
  padding: 40px;
  text-align: center;
  color: #333;
}
